import { createSlice } from "@reduxjs/toolkit";
import image1 from "../Componet/images/117-500x200.jpg";
import image2 from "../Componet/images/122-500x200.jpg";
import image3 from "../Componet/images/199-500x200.jpg";
import image4 from "../Componet/images/201-500x200.jpg";
import image5 from "../Componet/images/239-500x200.jpg";
import image6 from "../Componet/images/293-500x200.jpg";
import image7 from "../Componet/images/363-500x200.jpg";
import image8 from "../Componet/images/71-500x200.jpg";
import image9 from "../Componet/images/82-500x200.jpg";
import image10 from "../Componet/images/866-500x200.jpg";
import image11 from "../Componet/images/915-500x200.jpg";
const createSliceee = {
  image1,
  image2,
  image3,
  image4,
  image5,
  image6,
  image7,
  image8,
  image9,
  image10,
  image11,
};
export const Productmy = createSlice({
  name: "Product",
  initialState: createSliceee,
  reducers: {
    addimage: (state) => {
      return state.value;
    },
  },
});

export const { addimage } = Productmy.actions;

export default Productmy.reducer;
