import { Box, createTheme, ThemeProvider } from "@mui/material";
import { Stack } from "@mui/system";
import React, { useState } from "react";
import Add from "./Componet/Add/Add";
import Cardes from "./Componet/Cardes/Cardes";
import Navebar from "./Componet/Navebar/Navebar";
import Righibar from "./Componet/Righibar/Righibar";
import Sidbar from "./Componet/Sidbar/Sidbar";

const App = () => {
  const [mode, setmode] = useState("dark");
  const themeDark = createTheme({
    palette: {
      mode: mode,
    },
  });
  return (
    <ThemeProvider theme={themeDark}>
      <Box bgcolor="background.default" color={"text.primary"}>
        <Navebar setmode={setmode} mode={mode} />
        <Stack direction={"row"} spacing={1} justifyContent={"space-between"}>
          <Sidbar setmode={setmode} mode={mode} />
          <Cardes />
          <Righibar />
        </Stack>
        <Add />
      </Box>
    </ThemeProvider>
  );
};

export default App;
